/* --------------post2-------------------------------- */

body {
  margin: 0;
  box-sizing: border-box;
}

.thread {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 477px;
}

.input-section {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 150px;
}

.input-area {
  width: 200px;
  border-radius: 16px;
  height: 100px;
  background-color: #e4e2e2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: stretch;
  box-shadow: 1px 3px 8px rgba(0, 0, 0, 0.25);
}

.text-input {
  outline: none;
  border: none;
  border-radius: 16px;
  margin: 0;
  width: 95%;
  height: 80%;
  padding: 5px;
  box-shadow: 1px 3px 8px rgba(0, 0, 0, 0.25);
}

.icon-section {
  display: flex;
  justify-content: space-between;
  height: 50px;
  width: 100%;
}

.icon {
  display: flex;
  justify-content: space-around;
  width: 25%;
  height: 30px;
  align-items: center;
  margin-left: 50px;
}

.dropdown select {
  background-color: #fff;
  height: 30px;
  width: 40%;
  outline: none;
  border: none;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
}

.dropdown {
  height: 30px;
  width: 50%;
  display: flex;
  justify-content: space-around;
}

.big-txt {
  width: 90%;
  border-radius: 16px;
  height: 113px;
  background-color: #e4e2e2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: stretch;
  box-shadow: 1px 3px 8px rgba(0, 0, 0, 0.25);
}

.txtarea {
  outline: none;
  border: none;
  border-radius: 16px;
  margin: 0;
  width: 98%;
  height: 80%;
  box-shadow: 1px 3px 8px rgba(0, 0, 0, 0.25);
  padding: 5px;
}

#btn {
  background-color: #0029ff;
  color: #ffffff;
  border-radius: 10px;
  padding: 5px 4px;
  outline: none;
  border: none;
  width: 69px;
  font-size: 10px;
  text-align: center;
  height: 15px;
}

.btn-ctn {
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 80%;
}

.accordion {
  cursor: pointer;
  width: 478px;
  height: 40px;
  border: 0.1px solid rgba(0, 0, 0, 0.4);
  background: rgba(254, 255, 192, 0.2);
  color: #000;
  font-size: 20px;
  font-family: Open Sans;
  font-weight: 700;
  display: flex;
  align-items: center;
}

.accordion img{
  width: 55px;
  margin-right: 50px;
}

/* -------------------------post3------------------------ */

.form {
  width: 479px;
  display: flex;
  align-items: center;
  flex-direction: column;
}

#title {
  width: 422px;
  height: 43px;
  flex-shrink: 0;
  outline: none;
  border: none;
  border-radius: 5px;
  background: #FCFBFB;
  box-shadow: -2px 2px 6px 0px rgba(0, 0, 0, 0.15);
  margin-bottom: 7px;
}

.label {
  display: flex;
  width: 90%;
  height: 33px;
  flex-shrink: 0;
  color: #000;
  font-size: 16px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  letter-spacing: 0.32px;
  margin-top: 7px;
}

.text-nav {
  width: 422px;
  height: 233px;
  flex-shrink: 0;
  border-radius: 5px 5px 0px 0px;
  background: #FCFBFB;
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.18);
  padding: 2% 1%;
}

.text-nav textarea{
  width: 412px;
  height: 142px;
  flex-shrink: 0;
  margin-top: 5%;
}

.txt {
  display: flex;
  justify-content: space-around;
}

.txt a {
  text-decoration: none;
  color: gray;
}

.text-iconsS{
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 70%;
  margin-top: 3%;
}

.text-iconsS p{
  background-color: rgb(198, 198, 198);
  padding: 5px 5%;
}