* {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}

.header {
  width: 100%;
  position: sticky;
  height: 72px;
  left: 0px;
  top: 0px;
  margin: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
  padding: 16px 0px;
  background: #f0f0f0;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.nav {
  vertical-align: baseline;
  height: 49px;
  width: 1280px;
  margin: 0px 128px;
  padding: 0px 30.4px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-list svg {
  color: #3683f0;
  width: 25px;
  height: 25px;
}

.logo {
  width: 311px;
  height: 49px;
  left: 83px;
  top: 20px;
}

.right {
  height: 30.6125px;
  width: 212.575px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* -----------------------------main area---------------------- */

#content {
  height: 1417.3px;
  width: 1280px;
  margin: 0px 128px;
  padding: 0px;
  min-height: 100%;
}

.heading {
  width: 480px;
  height: 42px;
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 42px;
  letter-spacing: 0.02em;
  color: #0029ff;
}

.upper-section {
  position: absolute;
  left: 187px;
  top: 115px;
  width: 69%;
  height: 42px;
  display: flex;
  justify-content: space-between;
}

.btn {
  background-color: #0029ff;
  color: #ffffff;
  border-radius: 10px;
  padding: 5px 1.5%;
  outline: none;
  border: none;
}

/* --------------------box---------------- */

.box {
  position: absolute;
  width: 1066px;
  height: 135px;
  left: 187px;
  top: 187px;
  background: #e9ecef;
  border-radius: 5px;
}

.sub-heading {
  padding: 25px 0 0 27px;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 30px;
  letter-spacing: 0.02em;
  color: #000000;
}

.box-para {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  padding: 10px 27px 25px 27px;
  line-height: 21px;
  letter-spacing: 0.02em;
  color: #000000;
}

/* ------------------card section--------------- */

.post-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 3em;
  position: absolute;
  left: 214px;
  top: 397px;
}

.post {
  width: 480px;
  height: 580px;
  background: #fdfdfd;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px -4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 15px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.post-head {
  width: 480px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background: #000000;
  text-align: center;
  border-radius: 15px 15px 0px 0px;
  transition: display s ease-in-out;
}

.post-head p {
  color: #ffffff;
  width: 385px;
}

#post1 {
  width: 480px;
  height: 291px;
}

#post4 {
  width: 477px;
  height: 438px;
  border: none;
}

.post-para {
  font-family: sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: rgb(0, 0, 0);
  margin: 0px;
  padding: 21.3333px 16px;
}

.asset-content,
iframe {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
/* -------------form--------------- */

.sub-thread {
  bottom: 62.94%;
  background-color: rgba(0, 0, 0, 0.05);
  width: 199px;
  opacity: 0.5;
  border: 0.1px solid #333333;
  box-shadow: 1px 3px 8px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
}

/* -------------------page left-------------------- */

.dashboard {
  height: 652.6px;
  width: 113.6px;
  margin: 0px;
  padding: 0px;
  box-shadow: rgba(0, 0, 0, 0.314) 0px 0px 8px 0px;
  border-radius: 0 16px 16px 16px;
  position: sticky;
  top: 104px;
  background: #fdfdfd;
  z-index: 5;

  /* ------------gird ---------- */
  display: grid;
  grid-template-rows: 46px auto;
}

.head-d {
  background-color: #000;
  border-top-right-radius: 16px;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.h4 {
  color: #ffffff;
  text-align: center;
  width: 74%;
  display: none;
}

.head-d svg {
  width: 29px;
  height: 29px;
  background-color: #ffffff;
  border-radius: 50%;
  margin-right: 11px;
  cursor: pointer;
}

.bottom-d {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 282px;
  display: none;
}

/* -----------------------page right-------------------- */


.right-menu {
  position: fixed;
  width: 50px;
  height: 394px;
  right: 0px;
  top: 108px;
  background: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px -4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 30px 0px 0px 30px;

  display: grid;
  grid-template-columns: 50px auto;
}

.right-menu-head {
  background-color: #000;
  height: 100%;
  border-radius: 30px 0px 0px 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.right-menu-head svg {
  color: #ffffff;
  width: 25px;
  height: 25px;
  cursor: pointer;
}

.right-menu-head p {
  color: #ffffff;
  display: flex;
  flex-direction: column;
  height: 321px;
  width: 50px;
  align-items: center;
}

/* -----------------cta-menu---------------- */

.cta{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: baseline;
  width: 50px;
  height: 200px;
  position: fixed;
  right: 10px;
  top: 515px;
}

.cta-btn{
  background-color: blue;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cta-btn img{
  width: 35px;
}

/* ------------functions----------- */
.visible {
  display: flex;
}

.active-icon {
  transform: rotate(45deg);
}

.rotate-icon {
  transform: rotate(180deg);
}

.dashboard-active {
  width: 392px;
  height: 692px;
}

.right-menu-active {
  width: 300px;
}
